import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Config } from '../../app/config';

/**
 * Generated class for the InfosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-infos',
  templateUrl: 'infos.html',
})
export class InfosPage {
  public arrayImg : Array<any>;

  datas: Config[];
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.datas = [
      {name : 'Nombres de lignes', data : 15},
      {name : 'Nombres de colonnes', data : 15},
      {name : 'PNuméro de pin', data : 25}
    ];

    this.arrayImg = [
      {
        name : 'Thumbnail1',
        src: '/assets/imgs/thumbnail1.png',
     }
    ];
  
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InfosPage');
  }

  saveData(){

  }

}



